@import url("https://fonts.googleapis.com/css2?family=Fredoka&display=swap");

html {
  line-height: 1.15;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Fredoka", sans-serif;
}

body {
  background: linear-gradient(190deg, #35288f 0%, #4b3f7c 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #222;
  overflow: hidden;
}

.App {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
}
.App h1 {
  display: inline;
  text-align: center;
  margin-bottom: 2rem;
  color: #eef2f8;
  text-shadow: 0 0 5px #433aa8, 3px -1px 5px #271c6c;
}
.addTodos {
  display: flex;
  justify-content: center;
}

.todo-input {
  min-width: 15rem;
  width: 40vw;
  max-height: 2.5rem;
  background-color: #e1ebfd;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;

  align-self: center;
}

.todo-input:focus {
  outline: none;
  border: 2px solid #433aa8;
}

.add-btn {
  margin-left: 1rem;
  background-color: #271c6c;
  color: #e1ebfd;
  border-radius: 50%;
  border: 2px solid #e1ebfd;
  font-size: 1.5rem;
  width: 3.2rem;
  height: 3.2rem;
  cursor: pointer;
  box-shadow: 2px 4px 10px #271c6c;
  display: flex;
  justify-content: center;
  align-items: center;
}
.add-btn:focus {
  outline: none;
}
.display-todos {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.display-todos .buttons {
  margin-bottom: 2rem;
}
.display-todos .buttons button {
  padding: 0.5rem 1.2rem;
  border-radius: 20px;
  cursor: pointer;
  border: none;
}
.display-todos .buttons:focus {
  outline: none;
}
.display-todos .buttons button:not(:last-child) {
  margin-right: 1rem;
}

.display-todos ul {
  list-style: none;
  display: flex;
  align-self: flex-start;

  flex-wrap: wrap;
  margin-left: 5%;
  row-gap: 1rem;
}
.card {
  display: flex;
  flex-direction: column;
  background: rgba(180, 182, 218);
  background: radial-gradient(
    circle,
    hsla(237, 34%, 78%, 0.9) 0%,
    hsla(219, 88%, 94%, 0.9) 100%
  );

  margin: 0.1rem 1rem 0;
  height: 8rem;
  width: 18rem;
  border-radius: 0.5rem;
  padding: 1rem;
  position: relative;
}

.card textarea {
  padding: 0.5rem;
  border-radius: 8px;
  border: none;
  background-color: #e1ebfd;
  color: #271c6c;
  height: 70%;
}

.card textarea:disabled {
  background-color: transparent;
}

.card .btns {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}
.card .btns button {
  border: none;
  margin: 0 0.6rem;
  background-color: transparent;
  cursor: pointer;
}

.card .btns button:focus {
  outline: none;
}
.completed {
  position: absolute;
  right: 0.3rem;
  top: 0.3rem;
  background-color: #867bcd;
  border: 2px solid #272727;
  color: #e1ebfd;

  font-size: 0.8rem;
  padding: 0.3rem 1rem;
  border-radius: 20px;
}
.del {
  width: 30px;
  height: 30px;
  color: #f82068;
}
.ed {
  width: 30px;
  height: 30px;
  color: #0295b9;
}
.comp {
  width: 33px;
  height: 33px;
  color: #47b905;
}
